import { loadStytch, StytchClient } from '@stytch/stytch-js';
import React, { ComponentType, createContext, ReactNode, useContext, useEffect, useState } from 'react';

import { invariant } from 'utils/invariant';

type User = {
  user_id: string;
};
type Session = {
  session_id: string;
  expires_at: string;
};

const StytchMountedContext = createContext<boolean>(false);
const StytchUserContext = createContext<User | null>(null);
const StytchSessionContext = createContext<Session | null>(null);
const StytchContext = createContext<StytchClient | null>(null);

export const useStytchMounted__INTERNAL = (): boolean => useContext(StytchMountedContext);
export const useStytchRaw__INTERNAL = (): StytchClient | null => useContext(StytchContext);

export const useStytchUser = (): User | null => {
  invariant(useStytchMounted__INTERNAL(), 'useStytchUser/withStytchUser can only be used inside <StytchProvider>.');
  return useContext(StytchUserContext);
};
export const useStytchSession = (): Session | null => {
  invariant(
    useStytchMounted__INTERNAL(),
    'useStytchSession/withStytchSession can only be used inside <StytchProvider>.',
  );
  return useContext(StytchSessionContext);
};
export const useStytch = (): StytchClient | null => {
  invariant(useStytchMounted__INTERNAL(), 'useStytch/withStytch can only be used inside <StytchProvider>.');
  return useContext(StytchContext);
};

export const withStytch = <T extends object>(
  Component: ComponentType<T & { stytch: StytchClient | null }>,
): ComponentType<T> => {
  const WithStytch: ComponentType<T> = (props: T) => {
    return <Component {...props} stytch={useStytch()} />;
  };
  WithStytch.displayName = `withStytch(${Component.displayName || Component.name || 'Component'})`;
  return WithStytch;
};
export const withStytchUser = <T extends object>(
  Component: ComponentType<T & { stytchUser: User | null }>,
): ComponentType<T> => {
  const WithStytchUser: ComponentType<T> = (props: T) => {
    return <Component {...props} stytchUser={useStytchUser()} />;
  };
  WithStytchUser.displayName = `withStytchUser(${Component.displayName || Component.name || 'Component'})`;
  return WithStytchUser;
};
export const withStytchSession = <T extends object>(
  Component: ComponentType<T & { stytchSession: Session | null }>,
): ComponentType<T> => {
  const WithStytchSession: ComponentType<T> = (props: T) => {
    return <Component {...props} stytchSession={useStytchSession()} />;
  };
  WithStytchSession.displayName = `withStytchSession(${Component.displayName || Component.name || 'Component'})`;
  return WithStytchSession;
};

type StytchProviderProps = {
  publicToken: string;
  children?: ReactNode;
};

const LOCAL_STORAGE_KEY_PREFIX = 'stytch_state_';

const getLocalStorageKey = (type: 'session' | 'user', publicToken: string): string => {
  return `${LOCAL_STORAGE_KEY_PREFIX}${publicToken}_${type}`;
};

const getLocalStorageSession = (publicToken: string): Session | null => {
  if (typeof localStorage === 'undefined') return null;
  const sessionString = localStorage.getItem(getLocalStorageKey('session', publicToken));
  const session = JSON.parse(sessionString) as Session | null;
  if (session && Date.parse(session.expires_at) > Date.now()) {
    return session;
  }
  return null;
};

const getLocalStorageUser = (publicToken: string): User | null => {
  if (typeof localStorage === 'undefined') return null;
  if (getLocalStorageSession(publicToken)) {
    const userString = localStorage.getItem(getLocalStorageKey('user', publicToken));
    return userString ? (JSON.parse(userString) as User | null) : null;
  }
  return null;
};

export const StytchProvider = ({ publicToken, children }: StytchProviderProps): JSX.Element => {
  invariant(!useStytchMounted__INTERNAL(), 'You cannot render a <StytchProvider> inside another <StytchProvider>.');
  invariant(publicToken, 'publicToken not provided to <StytchProvider>.');

  const [stytchClient, setStytchClient] = useState<StytchClient | null>(
    typeof window !== 'undefined' && window.Stytch ? window.Stytch(publicToken) : null,
  );
  const [user, setUser] = useState<User | null>(
    stytchClient ? stytchClient.user.getSync() : getLocalStorageUser(publicToken),
  );
  const [session, setSession] = useState<Session | null>(
    stytchClient ? stytchClient.session.getSync() : getLocalStorageSession(publicToken),
  );

  useEffect(() => {
    if (!stytchClient) {
      loadStytch().then((globalStytch) => globalStytch && setStytchClient(globalStytch(publicToken)));
    }
  }, [stytchClient, publicToken]);

  useEffect(() => {
    if (stytchClient) {
      stytchClient.user.onChange(setUser);
      stytchClient.session.onChange(setSession);
    }
  }, [stytchClient]);

  return (
    <StytchMountedContext.Provider value={true}>
      <StytchContext.Provider value={stytchClient}>
        <StytchUserContext.Provider value={user}>
          <StytchSessionContext.Provider value={session}>{children}</StytchSessionContext.Provider>
        </StytchUserContext.Provider>
      </StytchContext.Provider>
    </StytchMountedContext.Provider>
  );
};
